<template>
  <div>
    <div id="map"></div>
  </div>
</template>

<script>
import L from "leaflet";
import "leaflet-routing-machine";

import { getNearbyBarApi } from "@/api.js";
import locationImg from "@/assets/img/position.png";

export default {
  async mounted() {
    const [lat, lng] = [53.3322511, -6.2575719];
    const zoom = 15;
    const colors = ["RED", "ORANGE", "GREEN", "BLUE", "INDIGO", "VIOLET"];
    const accessToken =
      "pk.eyJ1IjoiY2h5d2VpY2hlbiIsImEiOiJjazN1ZjlucDAwYzFpM2VxOGpsMHhyaTI4In0.ckLW5vTO6u2-omo0kwnGAA";
    let map = L.map("map").setView([lat, lng], zoom);
    L.tileLayer(
      `https://api.mapbox.com/styles/v1/{id}/tiles/{z}/{x}/{y}?access_token=${accessToken}`,
      {
        attribution:
          'Map data &copy; <a href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors, <a href="https://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>, Imagery Â© <a href="https://www.mapbox.com/">Mapbox</a>',
        id: "mapbox/streets-v11",
        minZoom: 12,
        accessToken: accessToken
      }
    ).addTo(map);
    /** POSITION MARKER BEGIN **/
    // position Icon
    let positionIcon = L.icon({
      iconUrl: locationImg,
      iconSize: [40, 40],
      iconAnchor: [20, 32],
      popupAnchor: [0, -20]
    });
    // position Marker Popup
    let positionPopup = L.popup().setContent(
      `<div class="position">Your position is <span>( ${lat}, ${lng} )</span></div>`
    );
    L.marker([lat, lng], {
      icon: positionIcon
    })
      .bindPopup(positionPopup)
      .addTo(map);
    /** POSITION MARKER END **/

    /** ROUTING BEGIN */
    // get nearby bar
    await getNearbyBarApi(lat, lng).then(res => {
      this.bars = res.data;
    });

    // create control
    let controls = [];
    for (let index in this.bars) {
      let waypoints = [
        L.latLng(lat, lng),
        L.latLng(this.bars[index].waypoint.lat, this.bars[index].waypoint.lng)
      ];
      let name = this.bars[index].name;
      let distance = this.bars[index].distance;
      controls.push(
        L.Routing.control({
          waypoints: waypoints,
          router: L.Routing.mapbox(accessToken),
          fitSelectedRoutes: false,
          plan: L.Routing.plan(waypoints, {
            draggableWaypoints: false,
            addWaypoints: false,
            createMarker: function(i, wp) {
              let best = index == 0 ? "best" : "";
              // if marker is user position
              if (i == 0) {
                return;
              }
              let routerMarker = L.marker(wp.latLng, {
                draggable: false
              })
                .bindTooltip(
                  `<span class="bold ${best}">${name}</span> [${parseFloat(
                    distance
                  ).toFixed(0)}m]`
                )
                .addTo(map)
                .openTooltip();
              return routerMarker;
            }
          }),
          lineOptions: {
            styles: [
              { color: "black", opacity: 0.3, weight: 9 },
              { color: "white", opacity: 0.8, weight: 6 },
              { color: colors[index], opacity: 1, weight: 2 }
            ]
          }
        }).addTo(map)
      );
    }
    // add to map
    for (let index in controls) {
      L.Routing.control(controls[index]).addTo(map);
    }
    /** ROUTING END */
  },
  data() {
    return {
      lat: 0,
      lng: 0,
      bars: []
      // showMap: true,
      // zoom: 15,
      // // center: latLng(0, 0),
      // mapOptions: {
      //   zoomSnap: 0.5
      // },
      // tileOptions: {
      //   minZoom: 12
      // },
      // url: "http://{s}.tile.osm.org/{z}/{x}/{y}.png",
      // attribution:
      //   '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors',
      // // position: icon({
      // //   iconUrl: positionImg,
      // //   iconSize: [32, 32],
      // //   iconAnchor: [16, 10]
      // // }),
      // waypoint: { lat: 0, lng: 0 },
      // bars: [],
      // colors: ["RED", "ORANGE", "GREEN", "BLUE", "INDIGO", "VIOLET"],
      // routingPlan: {
      //   draggableWaypoints: false
      // }
    };
  },
  methods: {}
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/index.scss";
</style>
